<template>
<div class="coupun container">
        <div class="vld-parent" ref="formContainer">
                   <loading :active.sync="isLoading" 
        :can-cancel="true" 
        :on-cancel="onCancel"
        :is-full-page="fullPage"></loading>
  <h2 class="pink-text center ">Redeem Voucher</h2>
  <div class="col-md-8">
    <h2 class="section-heading">Latest Coupon Codes and Deals</h2>
    <p>Your Current Point: <b class="green-text">{{currentp}}</b></p>
    <span><b>you can earn more point by joining any volunteer project </b><a href="/vproject"  > Click Here</a></span>
    <br>
    <br>
    <div class="coupons-list" v-for="(coupon,index) in displayedPosts" :key="index" >
      <div class="store-offer-item shadow-box">
        <div class="store-thumb-link">
          <div class="offer-image">
            <a href=""><img :src="coupon.imgv" alt="My Freedom Smoke" ></a>
          </div>
          <div class="store-name">
            <a class="blue-text"><b>{{coupon.namev}}</b></a>
          </div>
        </div>
        <div class="latest-coupon">
          <h3 class="coupon-title"><a>{{coupon.title}}</a></h3>
          <div class="coupon-des">
            {{coupon.descri}}
           
          </div>
        </div>
  <div class="coupon-detail coupon-button-type">
       <div class="blue-text">Required {{coupon.reqpoint}} Points <a ><i class="glyphicon glyphicon-star"></i></a></div>   
          <a class="coupon-deal coupon-button center" @click.prevent="getcoupun(coupon.id,coupon.totalv,coupon.imgv,coupon.code,coupon.reqpoint)" >Redeem Now</a>
          <div class="row exp-text">
            <div class="col 6"><span>Expired: <b> {{coupon.expired}} </b> </span></div>
            <div class="col 6"><span><b>{{coupon.totalv}} </b> : Coupons left</span></div>
          </div>
        
        </div>        

      </div>
    </div>
       		<ul class="pagination commentspage center">
				<li class="waves-effect nok">
					<a class="page-link" v-if="page != 1" @click="page--"> <i class="material-icons">chevron_left</i> </a>
				</li>
				<li class="waves-effect nok">
					<a  class="page-link" v-for="(pageNumber,index) in pages.slice(page-1, page+5)" @click="page = pageNumber" :key="index"> {{pageNumber}}</a>
				</li>
				<li class="waves-effect nok">
					<a   @click="page++" v-if="page < pages.length"> <i class="material-icons">chevron_right</i> </a>
				</li>
			</ul>
  </div>
  </div>
</div>  
</template>

<script>

import 'vue-loading-overlay/dist/vue-loading.css';
import Loading from 'vue-loading-overlay';
import M from 'materialize-css'
import db from '@/firebase/init'
 // eslint-disable-next-line no-unused-vars
import firebase from 'firebase'
export default {
  title: 'Redeem Voucher',
  name:'Coupun',
       mounted () {
  M.AutoInit()

    },
    data() {
        return {
        posts : [],
        page: 1,
        perPage: 6,
        pages: [],
        dell:'mo',
        currentp:0, 
       isLoading: false,
       fullPage: true                 
        }
    },
 components: {
            Loading
        },   
    methods :{
 doAjax() {
        this.isLoading = true;
                 
     },
   onCancel() {
              console.log('User cancelled the loader.')
    },
    getPosts (){

  const unlisten = db.collection("vouchers").onSnapshot(querySnapshot => {
    const allvouchers = querySnapshot
                    .docs.map((doc)=>({
                        id:doc.id,
                      ...doc.data(),
                    }));
                    
                    
                 this.posts=allvouchers
  })
   return ()  => unlisten()
    //     db.collection('vouchers').orderBy('expired')
    // .onSnapshot((snapshot) => {
    //   snapshot.docChanges().forEach(change => {
    //     if(change.type == 'added'){
    //       this.posts.unshift({
    //         code: change.doc.data().code,
    //         descri: change.doc.data().descri,
    //         expired:change.doc.data().expired,
    //         imgv: change.doc.data().imgv,
    //         namev: change.doc.data().namev,
    //         reqpoint:change.doc.data().reqpoint,   
    //         title:change.doc.data().title,    
    //         totalv:change.doc.data().totalv,
    //         id:change.doc.data().id
    //       })
    //     }
    //     if (change.type === "modified") {
    //       this.posts.unshift({
    //         code: change.doc.data().code,
    //         descri: change.doc.data().descri,
    //         expired:change.doc.data().expired,
    //         imgv: change.doc.data().imgv,
    //         namev: change.doc.data().namev,
    //         reqpoint:change.doc.data().reqpoint,   
    //         title:change.doc.data().title,   
    //         totalv:change.doc.data().totalv, 
    //         id:change.doc.data().id
    //       })               
    //     }
    //     if (change.type === "removed") {
    //       this.posts.unshift({
    //         code: change.doc.data().code,
    //         descri: change.doc.data().descri,
    //         expired:change.doc.data().expired,
    //         imgv: change.doc.data().imgv,
    //         namev: change.doc.data().namev,
    //         reqpoint:change.doc.data().reqpoint,   
    //         title:change.doc.data().title,   
    //         totalv:change.doc.data().totalv, 
    //         id:change.doc.id
    //       })     
    //     }
    //   })
    // }) 
  
    },
     setPages () {
       this.pages= []
			let numberOfPages = Math.ceil(this.posts.length / this.perPage);
			for (let index = 1; index <= numberOfPages; index++) {
				this.pages.push(index);
			}
		},
		paginate (posts) {

			let page = this.page;
			let perPage = this.perPage;
			let from = (page * perPage) - perPage;
			let to = (page * perPage);
			return  posts.slice(from, to);
    },
     // eslint-disable-next-line no-unused-vars
     getcoupun(couponid,couponnum,couponimg,couponcode,reqpoint){
        this.isLoading = true;
         let userid = firebase.auth().currentUser.uid
        if(this.currentp>=reqpoint){
          if(couponnum==1){
            db.collection("users").doc(userid).update({
            point:firebase.firestore.FieldValue.increment(-reqpoint)
            }) 
            db.collection("vouchers").doc(couponid).delete() 
            this.isLoading = false;
            this.$swal.fire({
            title: '<span style="font-weight: normal;" >Voucher Code: </span>'+couponcode,
            text: 'Please take pictures of this code or save it',
            imageUrl: couponimg,
            imageWidth: 400,
            imageHeight: 200,
            imageAlt: 'Custom image',
            })
         
          }else{
            db.collection("vouchers").doc(couponid).update({
            totalv:firebase.firestore.FieldValue.increment(-1)
            })
            db.collection("users").doc(userid).update({
            point:firebase.firestore.FieldValue.increment(-reqpoint)
            }) 
             this.isLoading = false;
            this.$swal.fire({
                title: '<span style="font-weight: normal;" >Voucher Code: </span>'+couponcode,
                text: 'Please take pictures of this code or save it',
                imageUrl: couponimg,
                imageWidth: 400,
                imageHeight: 200,
                imageAlt: 'Custom image',
                })              
                }

        }else{
               this.isLoading = false;
              this.$swal.fire(
             'Not Enought Point',
              "You can earn point by joining Volunteer project",
              'error'
              )          

        }

     }
 },
	computed: {
		displayedPosts () {
			return this.paginate(this.posts);
		}
	},
	watch: {
		posts () {
      if(this.dell){
        this.setPages();
      }else{
        console.log('masuk x')
      }
			
		}
  }, 
	created(){
      let userid = firebase.auth().currentUser.uid
        db.collection("users").doc(userid)
    .onSnapshot((doc) => {
        this.currentp=doc.data().point
    });

    this.getPosts()
    
	},
	filters: {
		trimWords(value){
			return value.split(" ").splice(0,20).join(" ") + '...';
		}
	}  
}
</script>
<style >

.coupun p{
 font-size: 1.5rem;
}
.section-heading {
    font-size: 21px;
    letter-spacing: -0.3px;
    font-weight: 300;
    margin-bottom: 14px;
}
.store-offer-item {
  padding: 20px 20px 16px 20px;
  margin-bottom: 15px;
  display:block;
}
.store-offer-item:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  
}
.shadow-box {
  
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  background: #fcfcfc;
  
}

.store-offer-item .store-thumb-link {
    float: left;
    margin-right: 20px;
}
.store-offer-item .store-thumb-link .offer-image {
    max-width: 226px;
    line-height: 0px;
    margin-bottom: 5px;
  text-align:center;
}
.offer-image a {
  border: 2px solid #f5f5f5;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  display: block;
}
.offer-image a:hover {
  border-color: #fe495d;
}
.offer-image img {
    width: 80%;
}
.offer-text {
  display:block;
  width:100%;
  height:100px;
  font-size:42px;
  line-height:36px;
  margin:10px 0 0 0;
  display: table-cell;
    text-align: center;
  font-family: 'Passion One', cursive;
}
.store-offer-item .store-thumb-link .store-name a {
    display: inline-block;
    line-height: 1;
    font-size: 13px;
    color: #1c9ab3;
    text-align: center;
    width:100%;
}
.store-offer-item .store-thumb-link .store-name a:hover {
  color:#fe495d;
}
.store-offer-item .store-thumb-link .store-name a span {
    overflow: hidden;
    text-overflow: ellipsis;
    width: 85%;
    display: block;
    float: left;
    white-space: nowrap;
}
.store-offer-item .store-thumb-link .store-name a i,
.des-more i{
  font-size:9px;
}

.store-offer-item .latest-coupon {
    max-width: 500px;
    margin-right: 20px;
  float: left;
  width: 100%;
  }
.latest-coupon .coupon-title {
  font-size: 16px;
  margin-bottom: 5px;
  font-weight: 500;
  margin-top: -2px;
  line-height: 1.5;
}
.latest-coupon .coupon-des {
    font-size: 13px;
    line-height: 1.7;
    position: relative;
}
.store-offer-item .coupon-detail {
    float: right;
  margin-top: 25px;
}
.coupon-button-type .coupon-deal, .coupon-button-type .coupon-print, .coupon-button-type .coupon-code {
    line-height: 1;
    padding: 14px 38px;
    background: #fc8c99;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: 600;
    display: inline-block;
    letter-spacing: 1px;
    text-transform: uppercase;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    margin-bottom: 2px;
    min-width: 195px;
    cursor: pointer;
}
.coupon-button-type .coupon-code {
  color: #444444;
  background: linear-gradient(315deg, rgba(0, 0, 0, 0.06) 10%, rgba(0, 0, 0, 0) 15%, rgba(0, 0, 0, 0) 35%, rgba(0, 0, 0, 0.06) 40%, rgba(0, 0, 0, 0.06) 60%, rgba(0, 0, 0, 0) 65%, rgba(0, 0, 0, 0) 85%, rgba(0, 0, 0, 0.06) 90%) repeat scroll 0 0/6px 6px rgba(0, 0, 0, 0);
  text-align: right;
  padding: 10px 12px;
  font-size: 20px;
  border: 2px solid #DDDDDD;
  position: relative;
}
.coupon-button-type .coupon-code .code-text {
  text-transform: uppercase;
}
.coupon-button-type .coupon-code .get-code {
  position: absolute;
  left: -2px;
  top: -2px;
  background: #fc8c99;
  color: #FFFFFF;
  font-size: 16px;
  padding: 14px 15px 14px 14px;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  transition: all 0.5s ease;
}
.coupon-button-type .coupon-code .get-code:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-top: 44px solid transparent;
  border-left: 44px solid #fc8c99;
  position: absolute;
  right: -44px;
  top: 0;
}
.coupon-button-type .coupon-code .get-code:hover {
  padding-right: 5px;
  transition: all 0.5s ease;
}
.coupon-detail .exp-text {
    font-size: 13px;
    display: block;
    margin-top: 5px;
    margin-bottom: -2px;
    color: #888888;
}
.coupon-save {
    float: right;
    font-size: 15px;
    margin-right: -4px;
    margin-top: -2px;
    color: #999999;
}
