    box-shadow: 0 0 0 1px transparent inset,0 0 0 0 rgba(39,41,43,.15) inset;
}
.coupon-listing-item .coupon-detail .user-ratting .ui.button {
    padding: 5px 10px 5px;
}
.ui.buttons .button {
    float: left;
    border-radius: 0;
